header {
  background-image: url(../../assets/images/hero-bg-min.jpg);
  background-position: center;
  background-size: cover;
}
header .top-header {
  padding: 40px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .header-title {
  text-transform: uppercase;
  height: 894px;
}
header .header-title h1 {
  margin-left: 20px;
  padding: 120px 0 40px;
  font-size: 52px;
  font-weight: 700;
  color: #fff;
}
header .btns-wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
header .btn-modal {
  text-align: start;
  min-width: 325px;
}
header .btn-modal {
  margin-top: 25px;
  font-size: 24px;
  padding: 8px 20px;
  color: #fff;
  background-color: #12293C;
  text-transform: uppercase;
}

@media (max-width: 1199px) {
  header .header-title h1 {
    font-size: 44px;
  }
}
@media (max-width: 991px) {
  header .header-title {
    height: 600px;
  }
  header .header-title h1 {
    padding: 60px 0 40px;
    font-size: 32px;
  }
  header .btn-modal {
    font-size: 22px;
  }
  header .btn-modal {
    min-width: 305px;
  }
}
@media (max-width: 767px) {
  header .header-title {
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  header .header-title h1 {
    padding: 0;
    margin-left: 0;
    font-size: 28px;
  }
}
@media (max-width: 575px) {
  header {
    background-position-x: -500px;
  }
  header .top-header {
    padding: 30px 0;
    flex-direction: column;
  }
  header .top-header div:first-child {
    margin-bottom: 30px;
  }
  header .btn-modal {
    font-size: 20px;
    text-align: center;
    min-width: 100%;
  }
}
footer {
  background-color: #12293C;
}
footer .row {
  padding: 100px 0;
  align-items: center;
}
footer .left-item {
  display: flex;
  align-items: center;
}
footer .no-personal-data {
  color: #8C8C8C;
  font-size: 20px;
}
footer .footer-item:last-child {
  text-align: end;
}
footer .description-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 930px;
  margin: 0 auto;
}
footer .description-wrap .description {
  padding: 40px 90px;
  background-color: #fff;
  font-size: 18px;
  border-radius: 3px;
  color: #12293C;
}
footer .description-wrap ul {
  padding-left: 25px;
}
footer .description-wrap .go-live-stream {
  margin: 40px 0;
}

@media (max-width: 991px) {
  footer .row {
    padding: 70px 0;
  }
  footer .footer-item:nth-child(1) {
    order: 1;
  }
  footer .footer-item:nth-child(2) {
    order: 3;
  }
  footer .footer-item:nth-child(3) {
    order: 2;
  }
  footer .no-personal-data {
    margin-top: 50px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  footer .row {
    padding: 50px 0;
  }
  footer .footer-item {
    text-align: center;
  }
  footer .footer-item:nth-child(3) {
    margin-top: 50px;
    text-align: center;
  }
  footer .description-wrap .description {
    padding: 15px 20px;
  }
}
@media (max-width: 575px) {
  footer .description-wrap .description {
    font-size: 15px;
  }
}
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Lato", sans-serif;
}

a:hover {
  text-decoration: none;
}

.btn {
  transition: all 0.2s ease-in-out;
}

.btn:hover {
  color: #fff;
  filter: brightness(120%);
}

.btn-red {
  color: #fff;
  background-color: #A5321A;
}